<template>
  <div class="card" v-for="article in articles" :key="article._id">
    <img :src="article.image" />

    <h3>{{ article.titre }}</h3>
    <p>{{ article.corps }}</p>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "MiniArticles",
  data() {
    return {
      articles: [],
    };
  },
  async mounted() {
    const response = await axios.get("http://localhost:3000/article");
    this.articles = response.data;
  },
};
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style-type: none;
}
.card {
   max-width: 280px;
   max-height: 490px;
    margin: 50px auto 0;
    text-align: center;
    font-size: 16px;
    display: flex;
    flex-direction: column;
   
}


</style>
