<script>
    import axios from 'axios'

    export default {
        name: 'Combattante',
        data(){
            return {
                combattantes: []
                
    
            }
        },
        async mounted(){
            const response = await axios.get('http://localhost:3000/combattante');
            this.combattantes = response.data;
        }
    };

</script>

